'use client'
import { TableCell, TableRow } from "@/components/ui/table";
import { Button } from "@/components/ui/button";
import { CategoryType } from "./CategoryList";
import { Badge } from "@/components/ui/badge";
import { Dialog, DialogClose, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Label } from "@/components/ui/label";
import { Input } from "@/components/ui/input";
import { Checkbox } from "@/components/ui/checkbox";

interface Props {
    category: CategoryType;
    index: number;
}

const CategoryRow = ({ category, index }: Props) => {


    const handleUpdateSubmit = async (name: string, status: number | string, id: number | string) => {
        const data = {
            name: name,
            status: status
        }


        const response = await axios.put(
            `${process.env.NEXT_PUBLIC_BACKEND_URL}/admin/update/category/${id}`,
            data,
            {
                headers: {
                    Authorization: `Bearer ${adminToken}`,
                    "Content-Type": "application/json"
                }
            }
        );

        console.log("Category updated successfully:", response.data);

    }


    return (
        <TableRow>
            <TableCell className="font-medium">{index + 1}</TableCell>
            <TableCell>{category.name}</TableCell>
            <TableCell>{category.status == 1 ?
                <Badge variant="secondary" className="bg-green-500 text-white dark:bg-green-600">
                    Active
                </Badge> :
                <Badge variant="destructive">
                    In Active
                </Badge>}</TableCell>
            <TableCell className="text-right">
                <Dialog>
                    <DialogTrigger asChild>
                        <Button>Edit</Button>
                    </DialogTrigger>
                    <form onSubmit={() => handleUpdateSubmit(category.name, category.status, category.id)}>
                        <DialogContent>
                            <DialogHeader>
                                <DialogTitle>Edit Category</DialogTitle>
                                <DialogDescription>Make changes to your category here. Click save when you&apos;re done.</DialogDescription>
                            </DialogHeader>
                            <div className="grid gap-4">
                                <div className="grid gap-3">
                                    <Label htmlFor="name-1">Name</Label>
                                    <Input id="name-1" name="name" defaultValue={category.name} />
                                </div>
                                <div className="grid gap-3">
                                    <Label className="hover:bg-accent/50 flex items-start gap-3 rounded-lg border p-3 has-[[aria-checked=true]]:border-blue-600 has-[[aria-checked=true]]:bg-blue-50 dark:has-[[aria-checked=true]]:border-blue-900 dark:has-[[aria-checked=true]]:bg-blue-950">
                                        <Checkbox
                                            id="toggle-2"
                                            defaultChecked={category.status == 1 ? true : false}
                                            className="data-[state=checked]:border-blue-600 data-[state=checked]:bg-blue-600 data-[state=checked]:text-white dark:data-[state=checked]:border-blue-700 dark:data-[state=checked]:bg-blue-700"
                                        />
                                        <div className="grid gap-1.5 font-normal">
                                            <p className="text-sm leading-none font-medium">
                                                Enable Category
                                            </p>
                                            <p className="text-muted-foreground text-sm">
                                                You can enable or disable category at any time.
                                            </p>
                                        </div>
                                    </Label>
                                </div>
                            </div>
                            <DialogFooter>
                                <DialogClose asChild>
                                    <Button variant="outline">Cancel</Button>
                                </DialogClose>
                                <Button type="submit">Save changes</Button>
                            </DialogFooter>
                        </DialogContent>
                    </form>
                </Dialog>
            </TableCell>
        </TableRow>
    )
}

export default CategoryRow;
